
.block {
    display: block;
    margin: 0;
    padding: 0;
}
.block-row > .block-area {
}
.block-col {
    text-align: left;
}
.block-col > .block {
    display: inline-block;
    vertical-align: top;
}
.block-area {
}
.block-clear:before,
.block-clear:after {
    content: '';
    display: table;
    clear: both;
}
.block-left {
    float: left;
}
.block-right {
    float: right;
}
.block-center {
    text-align: center;
}
.block-middle > *,
.block-center > * {
    display: inline-block;
    vertical-align: top;
}
.block-middle-container {
    position: relative;
}
.block-middle {
    width: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}



