
.examples {
}
.example {
    position: relative;
    margin: 20px 0 0 0;
    padding: 40px;
    border: 1px solid #adb5bd;
}
.example:before {
    content: 'EXAMPLE';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100px;
    height: 30px;
    text-align: center;
    color: #adb5bd;
    border-bottom: 1px solid #adb5bd;
    border-right: 1px solid #adb5bd;
    background: #FFF;
}
.code {
    display: block;
    position: relative;
    margin: 0 0 20px 0;
    padding: 20px 40px;
    text-align: left;
    border: 1px solid #adb5bd;
    border-top: none;
}



